import { useContext, createContext, useState } from "react";
import {
  makeStyles,
  AppBar,
  Toolbar,
  Typography,
  Button,
  Container,
  ListItem,
  ListItemIcon,
  ListItemText,
  List,
  IconButton,
} from "@material-ui/core";
import Logo from "../assets/logo.svg";
import { Link } from "react-router-dom";
import SearchBar from "./SearchBar";
import { isMobile } from "react-device-detect";
import { Drawer } from "@mui/material";
import SearchIcon from "@mui/icons-material/Search";
import MenuIcon from "@mui/icons-material/Menu";

import Divider from "@mui/material/Divider";
import ChevronLeftIcon from "@mui/icons-material/ChevronLeft";
import ModeNightIcon from "@mui/icons-material/ModeNight";
import FavoriteIcon from "@mui/icons-material/Favorite";
import {
  MUISwitch,
  Search,
  SearchIconWrapper,
  StyledInputBase,
} from "./CustomIcons";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    alignItems: "center",
    display: "flex",
  },
  logoImg: {
    color: "#fff",
    maxWidth: 50,
    display: "flex",
    alignItems: "center",
  },

  mobileLogo: {
    display: "flex",
    alignItems: "center",
    maxWidth: 35,
    marginLeft: 16,
  },
  tools: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
  },
  list: {
    display: "flex",
    alignItems: "center",
  },
  text: {
    marginLeft: 10,
    color: "#fff",
  },
  searchbar: {
    marginRight: 30,
  },
}));

const Navbar = ({ changePage }) => {
  const setDark = useContext(DarkContext);
  const setKeyword = useContext(SearchContext);
  const classes = useStyles();
  const [open, setOpen] = useState(false);

  const handleDrawer = () => {
    setOpen(!open);
  };

  return (
    <AppBar position="static">
      {!isMobile && (
        <Container fixed>
          <Toolbar>
            <div className={classes.root}>
              <Typography variant="h4">
                <Link to="/Crypto/" onClick={() => changePage(0)}>
                  <img src={Logo} alt="Logo" className={classes.logoImg} />
                </Link>
              </Typography>

              <Link
                to="/Crypto/favorites"
                style={{ textDecoration: "none", marginLeft: 20 }}
              >
                <Typography className={classes.text} variant="h6">
                  FAVORITES
                </Typography>
              </Link>
            </div>
            <SearchBar
              className={classes.searchbar}
              onChange={(e) => setKeyword(e)}
              onCancelSearch={(e) => setKeyword(e || "")}
            />
            <MUISwitch onChange={(e) => setDark(e.target.checked)} />
          </Toolbar>
        </Container>
      )}
      {isMobile && (
        <Toolbar className={classes.tools}>
          <Link to="/Crypto/">
            <img src={Logo} alt="Logo" className={classes.mobileLogo} />
          </Link>
          <ul className={classes.list}>
            <Search>
              <SearchIconWrapper>
                <SearchIcon />
              </SearchIconWrapper>
              <StyledInputBase
                onChange={(e) => setKeyword(e.target.value)}
                placeholder="Search..."
                inputProps={{ "aria-label": "search" }}
              />
            </Search>
            <IconButton
              color="inherit"
              onClick={handleDrawer}
              edge="start"
              className={classes.menuButton && classes.hide}
            >
              <MenuIcon />
            </IconButton>

            <Drawer variant="persistent" anchor="left" open={open}>
              <Toolbar className={classes.tools}>
                <IconButton onClick={handleDrawer}>
                  <ChevronLeftIcon />
                </IconButton>
              </Toolbar>
              <Divider />
              <List>
                <ListItem button>
                  <ListItemIcon>
                    <FavoriteIcon />
                  </ListItemIcon>
                  <ListItemText primary="Favorites" />
                </ListItem>
                <ListItem>
                  <ListItemIcon>
                    <ModeNightIcon />
                  </ListItemIcon>
                  <ListItemText primary="Dark Mode" />
                </ListItem>
              </List>
              <Divider />
            </Drawer>
          </ul>
        </Toolbar>
      )}
    </AppBar>
  );
};

export const SearchContext = createContext();
export const DarkContext = createContext();
export default Navbar;
